"use client"

import React, { ReactNode } from "react"

export function Button({ children, variant, className }: { children: ReactNode; variant?: string; className?: string }) {
  let baseClass = "px-4 py-2 rounded font-semibold focus:outline-none focus:ring-2 focus:ring-offset-2"
  let variantClass = ""

  switch (variant) {
    case "ghost":
      variantClass = "bg-transparent hover:bg-gray-100 text-gray-700"
      break
    case "outline":
      variantClass = "border border-gray-300 hover:bg-gray-100 text-gray-700"
      break
    case "default":
    default:
      variantClass = "bg-blue-600 text-white hover:bg-blue-700"
      break
  }

  return <button className={`${baseClass} ${variantClass} ${className || ""}`}>{children}</button>
}
