"use client"

import { useState, useRef, useEffect } from 'react'
import Image from 'next/image'
import { Button } from "@/components/ui/button"
import { Slider } from "@/components/ui/slider"
import { Volume2, VolumeX, Play, Pause, SkipBack, SkipForward } from 'lucide-react'

interface AudioPlayerProps {
  trackUrl?: string
  trackTitle?: string
  artistName?: string
  artworkUrl?: string
}

export default function AudioPlayer({ 
  trackUrl = "", 
  trackTitle = "No track selected", 
  artistName = "Unknown artist",
  artworkUrl = "https://images.pexels.com/photos/1626481/pexels-photo-1626481.jpeg?auto=compress&cs=tinysrgb&w=300"
}: AudioPlayerProps) {
  const [isPlaying, setIsPlaying] = useState(false)
  const [duration, setDuration] = useState(0)
  const [currentTime, setCurrentTime] = useState(0)
  const [volume, setVolume] = useState(1)
  const [isMuted, setIsMuted] = useState(false)
  
  const audioRef = useRef<HTMLAudioElement>(null)

  useEffect(() => {
    const audio = audioRef.current
    if (!audio) return

    const setAudioData = () => {
      setDuration(audio.duration)
      setCurrentTime(audio.currentTime)
    }

    const setAudioTime = () => setCurrentTime(audio.currentTime)
    
    // Add event listeners
    audio.addEventListener('loadeddata', setAudioData)
    audio.addEventListener('timeupdate', setAudioTime)

    // Cleanup
    return () => {
      audio.removeEventListener('loadeddata', setAudioData)
      audio.removeEventListener('timeupdate', setAudioTime)
    }
  }, [])

  const togglePlay = () => {
    if (!audioRef.current) return
    
    if (isPlaying) {
      audioRef.current.pause()
    } else {
      audioRef.current.play()
    }
    setIsPlaying(!isPlaying)
  }

  const toggleMute = () => {
    if (!audioRef.current) return
    
    if (isMuted) {
      audioRef.current.volume = volume
      setIsMuted(false)
    } else {
      audioRef.current.volume = 0
      setIsMuted(true)
    }
  }

  const handleVolumeChange = (newVolume: number[]) => {
    if (!audioRef.current) return
    
    const volumeValue = newVolume[0] / 100
    audioRef.current.volume = volumeValue
    setVolume(volumeValue)
    
    if (volumeValue === 0) {
      setIsMuted(true)
    } else if (isMuted) {
      setIsMuted(false)
    }
  }

  const handleTimeChange = (newTime: number[]) => {
    if (!audioRef.current) return
    
    const time = newTime[0]
    audioRef.current.currentTime = time
    setCurrentTime(time)
  }

  const formatTime = (time: number) => {
    if (isNaN(time)) return '0:00'
    
    const minutes = Math.floor(time / 60)
    const seconds = Math.floor(time % 60)
    return `${minutes}:${seconds.toString().padStart(2, '0')}`
  }

  return (
    <div className="fixed bottom-0 left-0 right-0 bg-white border-t shadow-lg audio-player">
      <div className="max-w-7xl mx-auto px-4 py-3">
        <div className="flex items-center justify-between">
          {/* Track Info */}
          <div className="flex items-center space-x-4">
            <div className="relative w-12 h-12">
              <Image 
                src={artworkUrl}
                alt={trackTitle}
                fill
                sizes="48px"
                className="rounded object-cover"
              />
            </div>
            <div>
              <h4 className="font-medium text-sm">{trackTitle}</h4>
              <p className="text-xs text-gray-500">{artistName}</p>
            </div>
          </div>

          {/* Player Controls */}
          <div className="flex flex-col items-center flex-1 max-w-2xl mx-8">
            <div className="flex items-center space-x-4 mb-2">
              <Button
                variant="ghost"
                size="icon"
                className="text-gray-600 hover:text-gray-900"
              >
                <SkipBack className="h-5 w-5" />
              </Button>
              
              <Button
                onClick={togglePlay}
                variant="outline"
                size="icon"
                className="h-10 w-10"
              >
                {isPlaying ? (
                  <Pause className="h-5 w-5" />
                ) : (
                  <Play className="h-5 w-5" />
                )}
              </Button>
              
              <Button
                variant="ghost"
                size="icon"
                className="text-gray-600 hover:text-gray-900"
              >
                <SkipForward className="h-5 w-5" />
              </Button>
            </div>

            <div className="w-full flex items-center space-x-2">
              <span className="text-xs text-gray-500 w-12 text-right">
                {formatTime(currentTime)}
              </span>
              
              <Slider
                value={[currentTime]}
                max={duration}
                step={1}
                className="flex-1"
                onValueChange={handleTimeChange}
              />
              
              <span className="text-xs text-gray-500 w-12">
                {formatTime(duration)}
              </span>
            </div>
          </div>

          {/* Volume Control */}
          <div className="flex items-center space-x-2">
            <Button
              variant="ghost"
              size="icon"
              onClick={toggleMute}
              className="text-gray-600 hover:text-gray-900"
            >
              {isMuted ? (
                <VolumeX className="h-5 w-5" />
              ) : (
                <Volume2 className="h-5 w-5" />
              )}
            </Button>
            
            <Slider
              value={[isMuted ? 0 : volume * 100]}
              max={100}
              className="w-24"
              onValueChange={handleVolumeChange}
            />
          </div>
        </div>
      </div>

      <audio
        ref={audioRef}
        src={trackUrl || undefined}
        preload="metadata"
      />
    </div>
  )
}
